import React from "react";
import { Link } from "react-router-dom";
import topImage from "../medias/merakist-jyoSxjUE22g-unsplash.jpg"; // Replace with your top image path

const DigitalMarketing = () => {
  return (
    <div className="p-4 bg-gray-100 min-h-screen md:w-4/5 md:m-auto">
      {/* Top Image Section */}
      <div className="mb-6">
        <img
          src={topImage}
          alt="Digital Marketing"
          className="w-full h-40 md:h-60 object-cover rounded"
        />
      </div>

      {/* Main Content Section */}
      <div className="bg-white p-6 rounded shadow">
        <h1 className="text-3xl font-bold mb-4">Marketing Digital</h1>

        <p className="mb-4">
          Dcouvrez nos services de marketing digital conus pour vous aider 
          atteindre vos objectifs en ligne. Nous utilisons les dernires
          stratgies et outils pour maximiser votre visibilit et votre
          engagement.
        </p>

        <h2 className="text-xl font-semibold mb-2">Nos Services</h2>
        <ul className="list-disc pl-6 mb-4">
          <li className="mb-2">
            Publicit sur les rseaux sociaux: Augmentez votre porte grce 
            des campagnes cibles.
          </li>
          <li className="mb-2">
            SEO (Optimisation pour les moteurs de recherche): Amliorez votre
            classement dans les rsultats de recherche.
          </li>
          <li className="mb-2">
            Marketing par e-mail: Engagez vos clients avec des newsletters et
            des promotions personnalises.
          </li>
          <li className="mb-2">
            Analyse des donnes: Utilisez les donnes pour affiner vos
            stratgies et mesurer les performances.
          </li>
        </ul>

        <h2 className="text-xl font-semibold mb-2">
          Pourquoi Choisir Nos Services
        </h2>
        <p className="mb-4">
          Nous offrons une approche personnalise pour chaque client, en nous
          concentrant sur des solutions adaptes  vos besoins spcifiques et en
          vous aidant  atteindre vos objectifs de manire efficace.
        </p>

        <h2 className="text-xl font-semibold mb-2">Contactez-Nous</h2>
        <p>
          Pour en savoir plus sur nos services de marketing digital ou pour
          discuter de vos besoins, veuillez nous contacter {" "}
          <Link to="/contact-us" className="text-blue-600 hover:underline">
            Contact
          </Link>
          .
        </p>
      </div>
    </div>
  );
};

export default DigitalMarketing;
