import React from "react";

const JobCard = ({ title, description }) => {
  return (
    <div className="job-card bg-white p-6 rounded-lg shadow-lg mb-6 md:w-4/5 md:mx-auto md:space-y-6">
      <h2 className="text-2xl font-semibold mb-4 text-blue-800">{title}</h2>
      <p className="text-gray-700">{description}</p>
    </div>
  );
};

const CareerSection = () => {
  const jobOpenings = [
    {
      title: "Data Analyst",
      description:
        "Nous recherchons un analyste de donnes talentueux pour rejoindre notre quipe. Vous serez responsable de l'analyse des donnes, de l'extraction d'informations et de la cration de rapports pour aider nos clients  prendre des dcisions claires.",
    },
    {
      title: "Web Developer",
      description:
        "Nous avons besoin d'un dveloppeur web cratif et motiv pour concevoir, dvelopper et maintenir nos applications web. Vous travaillerez avec des technologies modernes pour crer des interfaces utilisateur intuitives et des expriences utilisateur de qualit.",
    },
    {
      title: "Cyber Security Tutor",
      description:
        "Nous recherchons un formateur en cyberscurit passionn pour enseigner et guider nos tudiants sur les meilleures pratiques en matire de scurit informatique. Vous devez avoir une solide comprhension des concepts de scurit, des menaces actuelles et des solutions de protection.",
    },
  ];

  return (
    <div className="career-section p-4 bg-gray-100 min-h-screen">
      <h1 className="text-3xl font-bold mb-6 text-center text-blue-800">
        Nous Recherchons des Collaborateurs Talentueux et Dvous
      </h1>
      <p className="text-center mb-8 text-gray-700">
        GenieStat est  la recherche de personnes passionnes pour rejoindre
        notre quipe. Si vous tes motiv, travailleur, et souhaitez faire
        partie de notre equipe, nous serions ravis de vous rencontrer.
      </p>
      {jobOpenings.map((job, index) => (
        <JobCard key={index} title={job.title} description={job.description} />
      ))}
      <div className="text-center mt-8">
        <a
          href="/contact"
          className="text-blue-500 hover:text-blue-700 font-semibold"
        >
          Contactez-nous pour postuler
        </a>
      </div>
    </div>
  );
};

export default CareerSection;
